@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.about {
  padding: 2em;
  width: 100vw;
  min-height: 70vh;
  background-color: white;
}

.btn-link{
  font-size: 24px;
  text-align: left;
}

.body {
  height: 240px;
}

#aboutAccordion {
  width: 70%;
}

#headshot img {
  width: 100%;
}

#nameTitle {
  text-align: left;
  font-size: 50px;
  margin: 0.5em;
  margin-bottom: 1em;
  font-weight: bold;
}

.skills {
  padding: 2em;
  min-height: 20vh;
  background-color: black;
  color: white;
}

.skills img {
  background-color: white;
  margin-top: 1em;
  height: 100px;
}

.contact {
  padding: 2em;
  min-height: 10vh;
  background-color: black;
  color: white;
}

.contact a {
  color: white;
}

.projectImages {
  margin-bottom: 20px;
}

li, p {
  text-align: left;
}

.projects {
  padding: 2em;
  min-height: 100vh;
  background-color: #246;
  width: 100%;
}

.projects h2 {
  margin-bottom: 1.6em;
  color: white;
}

.card-body:hover {
  cursor: pointer;
}

.techList {
  list-style-type: square;
}

#details {
  text-align: left;
}

#projectDisplay {
  margin: 2em;
  margin-left: 1em;
  margin-bottom: 20px;
  vertical-align: top;
}

#displayTitle {
  font-size: 34px;
  margin: 0.5em;
}

* {
  text-align: center;
  transition: 0.5s;
  font-family: 'Roboto', sans-serif;
}

#blackBar {
  background-color: black;
  height: 6vh;
}

